.work-container {
  padding: 5rem;
}
.project-heading {
  text-align: center;
  padding: 4rem 0 2rem 0;
}
.project-container {
  min-width: 114px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 40px;
  padding-bottom: 5rem;
}

.project-card {
  background-color: #1a1919;
  padding: 1.2rem 1rem;
  border-radius: 20px;
  height: fit-content;
}
.project-card img {
  width: 100%;
  height: 50%;
  border-radius: 15px;
}
.project-title {
  color: #fff;
  padding: 1rem 0;
}
.pro-details {
  padding-bottom: 1rem;
  font-size: 1.1rem;
  text-align: justify;
}
.pro-btns {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.pro-btns .btn {
  padding: 0.5rem 1rem;
}

@media (max-width: 1220px) {
  .project-container {
    width: 100%;

    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 740px) {
  .project-container {
    max-width: 90%;
    margin: auto 0;
    grid-template-columns: 1fr;
  }
}
