.about {
  width: 100%;
  margin: 3rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  height: 100%;
}
.left-about {
  text-align: center;
  margin: auto;
  padding: 1rem;
  max-width: fit-content;

  /* max-width: 350px; */
  /* max-width: 500px; */
}
.left-about p {
  margin: 1.2rem 0;
  padding-bottom: 1rem;
}
.right-about {
  max-width: 700px;
}
.left-about .btn {
  /* width: fit-content; */
  margin: 1rem 0.2rem;
}
.img-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  position: relative;
  align-items: center;
  text-align: center;
}
.img-about {
  max-width: 90%;
  border: 1px solid #333;
}
.top {
  grid-column: 1 / span 8;
  grid-row: 1;
  padding-top: 20%;
  z-index: 6;
}
.bottom {
  grid-column: 4 / -1;
  grid-row: 1;
  z-index: 5;
}
@media (max-width: 415px) {
  .left-about .btn {
    display: flex;
    flex-direction: column;
    margin: auto;
  }
}
